<script setup>
/* eslint-disable */
import { ref, nextTick, onMounted } from 'vue';
import Swal from 'sweetalert2';
import Dialogue from './Dialogue.vue';
import AudioCard from './AudioCard.vue';
import Title from './Title.vue';
const props = defineProps(['gameStatus', 'path']);
const list = ref([]);
const index = ref(-1);
const dialogueContainer = ref(null);
let story = {}
let maxNum = 0;
let cate = 0;
const audioSrc = ref("田中ユウスケ _ agehasprings - おしんこ (Inst_).mp3");
const bg = ref('视频/2A200251C89C0662951BD413036A697F.jpg');
const choosing = ref(false);
const T = ref("");
const isMusicPlaying = ref(true);
const lastMusicSrc = ref("田中ユウスケ _ agehasprings - おしんこ (Inst_).mp3"); // 记住最后播放的音乐
onMounted(() => {
  console.log(props.path);
  story = props.path;
  T.value = Object.keys(story)[cate++];
  console.log(T.value);
  maxNum = story[T.value].length;
})
function NextDialogue() {

  if (index.value != -1 && story[T.value][index.value]["goto"] == -1) {
    // 检查是否是分支选择，如果是则不自动切换章节
    if (story[T.value][index.value]["type"] == "Choice") {
      return; // 停止执行，等待用户选择
    }
    T.value = Object.keys(story)[cate++];
    console.log(T.value);
    if (story[T.value] && story[T.value].length) {
      maxNum = story[T.value].length;
    } else {
      console.error('Chapter not found:', T.value);
      return;
    }
    index.value = -1;
  }
  if (index.value >= 1 && story[T.value][index.value]["goto"] == "End") {
    Swal.fire({
      title: "Ending!!!",
      width: 600,
      padding: "3em",
      color: "#716add",
      background: "#fff ",
      backdrop: `
    rgba(0,0,123,0.4)
    left top
    no-repeat
  `
    });
    return;
  }
  if (index.value != -1) index.value = story[T.value][index.value]["goto"];
  else index.value++;
  list.value.push(story[T.value][index.value]);

  if (story[T.value][index.value].type == 'Choice') {
    choosing.value = true;
  }
  if (story[T.value][index.value].event != null) {
    const e = story[T.value][index.value].event;
    console.log(e);
    e.forEach(element => { EventHandler(element.e, element.val) })
  }
  nextTick(() => {
    if (dialogueContainer.value) {
      dialogueContainer.value.scrollTop = dialogueContainer.value.scrollHeight;
    }
  });
}

function JumpToNextDialogue(value, index_) {
  console.log('JumpToNextDialogue called with:', value, index_);
  
  // 如果value是字符串（分支名称），需要切换到对应的分支
  if (typeof value === 'string') {
    T.value = value;
    console.log('Switching to branch:', T.value);
    if (story[T.value] && story[T.value].length) {
      maxNum = story[T.value].length;
    } else {
      console.error('Branch not found:', T.value);
      return;
    }
    index.value = -1;
    choosing.value = false;
    return;
  }
  
  index.value = value;
  choosing.value = false;
  console.log(list.value);
  console.log(index_);
  list.value.pop();
  list.value.push(story[T.value][value]);
  if (index.value != -1 && story[T.value][index.value]["goto"] == -1) {
    T.value = Object.keys(story)[cate++];
    console.log(T.value);
    index.value = -1;
    return;
  }
  if (index.value >= 1 && story[T.value][index.value]["goto"] == "End") {
    Swal.fire({
      title: "Ending!!!",
      width: 600,
      padding: "3em",
      color: "#716add",
      background: "#fff ",
      backdrop: `
    rgba(0,0,123,0.4)
    left top
    no-repeat
  `
    });
    return;
  }
  if (story[T.value][index.value].type == 'Choice') {
    choosing.value = true;
  }
  if (story[T.value][index.value].event != null) {
    const e = story[T.value][index.value].event;
    console.log(e);
    e.forEach(element => { EventHandler(element.e, element.val) })
  }
  nextTick(() => {
    if (dialogueContainer.value) {
      dialogueContainer.value.scrollTop = dialogueContainer.value.scrollHeight;
    }
  });
  console.log(list.value);
  console.log(value);
}

function EventHandler(e, val) {
  if (e == "AudioPlay") {
    audioSrc.value = val;
    lastMusicSrc.value = val; // 记住当前播放的音乐
  } else if (e == "ChangeBG") {
    bg.value = val;
  }
}

function toggleMusic() {
  isMusicPlaying.value = !isMusicPlaying.value;
  // 通过修改audioSrc来触发AudioCard的重新渲染
  if (isMusicPlaying.value) {
    audioSrc.value = audioSrc.value || "田中ユウスケ _ agehasprings - おしんこ (Inst_).mp3";
  } else {
    audioSrc.value = "";
  }
}

function pauseMusic() {
  isMusicPlaying.value = false;
  audioSrc.value = "";
}

function resumeMusic() {
  isMusicPlaying.value = true;
  audioSrc.value = lastMusicSrc.value; // 恢复之前播放的音乐
}
</script>

<template>
  <div>
    <div>
      <div :style="{ backgroundImage: bg ? `url(${bg})` : 'none', backgroundColor: bg ? 'transparent' : 'black' }"
        class="background-image-layer"></div> <!-- 背景层 -->
      <div class="background">
        <el-row class="gradient-row">
          <el-col :span="5" class="other-col"></el-col>
          <el-col :span="1" class="side-col-left"></el-col>
          <el-col :span="12" class="center-col">
            <Title :title="T"></Title>
            <div ref="dialogueContainer" class="dialogue-container">
              <TransitionGroup name="list">
                  <div v-for="(i, index) in list" :key="i">
                    <Dialogue :type="i.type" :text="i.text" :jump="i.jump" @jumpto="JumpToNextDialogue" :index="index"
                      :color="i.color" :avatar="i.avatar" @pauseMusic="pauseMusic" @resumeMusic="resumeMusic" />
                  </div>
              </TransitionGroup>
            </div>
            <button class="goon" v-if="choosing == false" @click="NextDialogue">Next</button>
          </el-col>
          <el-col :span="1" class="side-col-right"></el-col>
          <el-col :span="5" class="other-col">
            <AudioCard :source=audioSrc></AudioCard>
            <div class="music-control">
              <button @click="toggleMusic" class="music-btn">
                {{ isMusicPlaying ? '⏸️' : '▶️' }}
              </button>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>
