    },
    {
      "text": "我们机构就经常给看好的年轻人提供这样的...视野。",
      "type": "Right",
      "avatar": "李",
        "goto": 54
    },
    {
      "text": "哦？比如呢？",
      "type": "Right",
      "avatar": "张",
      "goto": 55
    },
    {
      "text": "今天先聊大的方向，具体以后有机会深入合作再说。来，加个微信，方便以后联系。",
      "type": "Right",
      "avatar": "李",
      "goto":56
    },
    {
      "text": "李维成功添加了张振的联系方式。",
      "type": "Center",
      "goto": -1
    }
  ,

    {
      "text": "师兄，我们下午不是约了要和陈教授讨论问题吗？时间快到了",
      "type": "Left",
      "avatar": "林",
      "goto": 58
    },
    {
      "text": "哦对！瞧我这记性！差点把正事忘了。李...李维是吧？不好意思啊，今天导师有安排，下次，下次再聊！",
      "type": "Right",
      "avatar": "张",
      "goto": 59
    },
    {
      "text": "当然当然，导师的事要紧。那我们下次再约。很高兴认识二位。",
      "type": "Right",
      "avatar": "李",
      "goto": 60
    },
    {
      "text": "李维保持着得体的微笑，与两人告别，转身离开。他的眼神在转身瞬间变得若有所思。",
      "type": "Center",
      "goto": 61
    },
    {
      "text": "可惜了，感觉他懂的还挺多，说不定能聊出点新想法。",
      "type": "Right",
      "avatar": "张",
      "goto": 62
    },
    {
      "text": "师兄，还是谨慎点好。咱们的项目...",
      "type": "Left",
      "avatar": "林",
      "goto": 63
    },
    {
      "text": "哎呀知道知道，保密嘛！就聊点行业大势，不涉及具体的。",
      "type": "Right",
      "avatar": "张",
      "goto": 64
    },
    {
      "text": "再说了，人家是投资人，路子广，认识一下没坏处。",
      "type": "Right",
      "avatar": "张",
      "goto": 65
    },
    {
      "text": "林枫微微皱眉，但见张振如此说，也不好再坚持，心里却隐隐埋下了一颗警惕的种子。",
      "type": "Center",
      "goto": -1
    }
  ],
  "芯蚀-第五幕": [
    {
      "text": "几天后，林枫独自一人在'静岸咖啡馆'看书。",
      "type": "Center",
      "goto": 1
    },
    {
      "text": "李维'恰好'推门而入。",
      "type": "Center",
      "goto": 2
    },
    {
      "text": "林枫？真巧啊！你也喜欢来这里？",
      "type": "Right",
      "avatar": "李",
      "goto": 3
    },
    {
      "text": "李先生。这里比较安静。",
      "type": "Left",
      "avatar": "林",
      "goto": 4
    },
    {
      "text": "别那么见外，叫李维就行。上次没聊尽兴，今天正好碰上。",
      "type": "Right",
      "avatar": "李",
      "goto": 5
    },
    {
      "text": "你们导师抓得挺紧啊，项目很忙吧？",
      "type": "Right",
      "avatar": "李",
      "goto": 6
    },
    {
      "text": "还好，学业任务总是有的。",
      "type": "Left",
      "avatar": "林",
      "goto": 7
    },
    {
      "text": "我听说你们陈教授牵头那个'玄武'项目，可是瞄准了世界级难题啊。真是厉害。",
      "type": "Right",
      "avatar": "李",
      "goto": 8
    },
    {
      "text": "像你们这样能参与其中的学生，前途不可限量。",
      "type": "Right",
      "avatar": "李",
      "goto": 9
    },
    {
      "text": "李先生，我们只是做一些基础辅助工作，核心内容不清楚。",
      "type": "Left",
      "avatar": "林",
      "goto": 10
    },
    {
      "text": "谦虚了。这样的项目，随便流出来一点边角料，都够在顶级期刊发文章了。",
      "type": "Right",
      "avatar": "李",
      "goto": 11
    },
    {
      "text": "或者...换个赛道，能带来的价值更是不可估量。",
      "type": "Right",
      "avatar": "李",
      "goto": 12
    },
    {
      "text": "李维意味深长地看着林枫，手指无意识地在桌面上敲了敲。",
      "type": "Center",
      "goto": 13
    },
    {
      "text": "李先生，我不知道你究竟想说什么。但我们做的只是普通的学术研究，没什么特别。",
      "type": "Left",
      "avatar": "林",
      "goto": 14
    },
    {
      "text": "我还有事，先走了。",
      "type": "Left",
      "avatar": "林",
      "goto": 15
    },
    {
      "text": "林枫同学，机会不是每天都有的。有时候，选择比努力更重要。",
      "type": "Right",
      "avatar": "李",
      "goto": 16
    },
    {
      "text": "我可以给你提供一个无法拒绝的...",
      "type": "Right",
      "avatar": "李",
      "goto": 17
    },
    {
      "text": "不必了。我对现在的研究很满意。再见。",
      "type": "Left",
      "avatar": "林",
      "goto": 18
    },
    {
      "text": "林枫毫不犹豫地转身离开，留下李维站在原地，脸色逐渐阴沉。",
      "type": "Center",
      "goto": -1
    }
  ],
  "芯蚀-第六幕": [
    {
      "text": "大学校园一角，傍晚。",
      "type": "Center",
      "goto": 1,
      "event": [{"e": "ChangeBG", "val": "视频/0C86CE6ED687F7C9AD4842FB18A43C20.jpg"}]
    },
    {
      "text": "张振刚走出实验室，李维从一旁走出，神情严肃而焦急。",
      "type": "Center",
      "goto": 2
    },
    {
      "text": "张振师兄！可算等到你了！有件非常紧急的事，必须让你知道！",
      "type": "Right",
      "avatar": "李",
      "goto": 3
    },
    {
      "text": "李维？什么事这么急？",
      "type": "Left",
      "avatar": "张",
      "goto": 4
    },
    {
      "text": "这里不方便。是关于你师弟林枫的，还有你们的项目。",
      "type": "Right",
      "avatar": "李",
      "goto": 5
    },
    {
      "text": "林枫？项目？你到底想说什么？",
      "type": "Left",
      "avatar": "张",
      "goto": 6
    },
    {
      "text": "你看这个。这是我一个朋友偶然从国外一个灰色地带的技术论坛截获的。",
      "type": "Right",
      "avatar": "李",
      "goto": 7
    },
    {
      "text": "发布者很隐蔽，但内容看起来非常像你们项目的B部分核心算法！",
      "type": "Right",
      "avatar": "李",
      "goto": 8
    },
    {
      "text": "这...这不可能！这看起来...",
      "type": "Left",
      "avatar": "张",
      "goto": 9
    },
    {
      "text": "我也不愿相信。但万一呢？万一林枫同学一时糊涂，或者...是被黑客攻击了？",
      "type": "Right",
      "avatar": "李",
      "goto": 10
    },
    {
      "text": "这事关重大，不仅关乎项目，更关乎你们的前程！",
      "type": "Right",
      "avatar": "李",
      "goto": 11
    },
    {
      "text": "那...那怎么办？我得立刻告诉教授！",
      "type": "Left",
      "avatar": "张",
      "goto": 12
    },
    {
      "text": "等等！师兄，冷静！现在告诉教授，如果万一只是个误会，或者只是相似的技术，你不是害了林枫吗？",
      "type": "Right",
      "avatar": "李",
      "goto": 13
    },
    {
      "text": "而且无凭无据。",
      "type": "Left",
      "avatar": "李",
      "goto": 14
    },
    {
      "text": "那...那你说怎么办？",
      "type": "Right",
      "avatar": "张",
      "goto": 15
    },
    {
      "text": "我们得先确认。这样，你手里不是有A部分吗？",
      "type": "Left",
      "avatar": "李",
      "goto": 16
    },
    {
      "text": "你把A部分的接口定义和框架图给我，不用全部，就关键几页。",
      "type": "Right",
      "avatar": "李",
      "goto": 17
    },
    {
      "text": "我找我那个技术大牛朋友立刻做个交叉验证，看看这泄露的B部分和你的A部分是否能严丝合缝地对上。",
      "type": "Left",
      "avatar": "李",
      "goto": 18
    },
    {
      "text": "如果能对上，那基本就坐实了，我们再拿着证据一起去找陈教授！",
      "type": "Left",
      "avatar": "李",
      "goto": 19
    },
    {
      "text": "如果对不上，那就虚惊一场，谁也别说，免得伤了和气。",
      "type": "Left",
      "avatar": "李",
      "goto": 20
    },
    {
      "text": "张振内心极度挣扎，一方面觉得不妥，另一方面又被巨大的恐惧和'求证'心态主导。",
      "type": "Center",
      "goto": 21
    },
    {
      "text": "...这...违反规定...",
      "type": "Right",
      "avatar": "张",
      "goto": 22
    },
    {
      "text": "规定重要，还是项目安全、你们的清白重要？这是在补救！是在保护他，也是在保护你！",
      "type": "Right",
      "avatar": "李",
      "goto": 23
    },
    {
      "text": "张振犹豫再三，最终一咬牙。",
      "type": "Center",
      "goto": 24
    },
    {
      "text": "...好！但我只给你最基础的框架图，而且你看完必须立刻删除！马上验证！",
      "type": "Right",
      "avatar": "张",
      "goto": 25
    },
    {
      "text": "放心！都是为了项目。你快回去准备，发到我这个加密邮箱，越快越好！",
      "type": "Right",
      "avatar": "李",
      "goto": 26
    },
    {
      "text": "当晚，林枫在宿舍收到匿名邮件。",
      "type": "Center",
      "goto": 27
    },
    {
      "text": "看到'A部分参考'的字样和内容后，他大惊失色，立刻致电张振。",
      "type": "Center",
      "goto": 28
    },
    {
      "text": "两人在实验室外的走廊相遇。",
      "type": "Center",
      "goto": 29
    },
    {
      "text": "师兄！这你怎么解释？是不是你给他的？",
      "type": "Left",
      "avatar": "林",
      "goto": 30
    },
    {
      "text": "林枫！你血口喷人！我还想问你呢！是不是你的B部分泄了，想来套我的A部分？",
      "type": "Right",
      "avatar": "张",
      "goto": 31
    },
    {
      "text": "贼喊捉贼！",
      "type": "Right",
      "avatar": "张",
      "goto": 32
    },
    {
      "text": "邮件发到我这里！内容是你负责的部分！不是你是谁？",
      "type": "Left",
      "avatar": "林",
      "goto": 33
    },
    {
      "text": "我怎么知道是谁！说不定是你自己搞鬼！",
      "type": "Right",
      "avatar": "张",
      "goto": 34
    },
    {
      "text": "教授再三强调保密，我怎么可能做这种事！你太让我失望了！",
      "type": "Right",
      "avatar": "张",
      "goto": 35
    },
    {
      "text": "失望？我才失望！没想到你是这种人！",
      "type": "Left",
      "avatar": "林",
      "goto": 36
    },
    {
      "text": "两人的争吵声引来了还未离开的陈教授。",
      "type": "Center",
      "goto": 37
    },
    {
      "text": "吵什么！实验室门口，像什么样子！进来再说！",
      "type": "Center",
      "avatar": "陈",
      "goto": 38
    },
    {
      "text": "在陈教授办公室，林枫说明了情况，张振在一旁气得别过头去。",
      "type": "Center",
      "goto": 39
    },
    {
      "text": "好险恶的手段。",
      "type": "Center",
      "avatar": "陈",
      "goto": 40
    },
    {
      "text": "教授？",
      "type": "Left",
      "avatar": "林",
      "goto": 41
    },
    {
      "text": "教授？",
      "type": "Right",
      "avatar": "张",
      "goto": 42
    },
    {
      "text": "这里，这里，还有这里。我在原始设计里埋了'逻辑雷区'和'数字指纹'，极其隐蔽。",
      "type": "Center",
      "avatar": "陈",
      "goto": 43
    },
    {
      "text": "这份资料里，这些要么错了，要么没了。这是一份用AI生成的、足够以假乱真的伪造品。",
      "type": "Center",
      "avatar": "陈",
      "goto": 44
    },
    {
      "text": "伪造的？",
      "type": "Left",
      "avatar": "林",
      "goto": 45
    },
    {
      "text": "...是谁！谁要这么害我们？",
      "type": "Right",
      "avatar": "张",
      "goto": 46
    },
    {
      "text": "目的很清楚：离间...你们差点就上当了。",
      "type": "Center",
      "avatar": "陈",
      "goto": 47
    },
    {
      "text": "师兄...对不起...我...",
      "type": "Left",
      "avatar": "林",
      "goto": 48
    },
    {
      "text": "算了...我也...我也太冲动了。",
      "type": "Right",
      "avatar": "张",
      "goto": 49
    },
    {
      "text": "立刻全面核查所有终端和存储设备的安全隔离。",
      "type": "Center",
      "avatar": "陈",
      "goto": 50
    },
    {
      "text": "这件事，我会立刻向上级和安全部门汇报。",
      "type": "Center",
      "avatar": "陈",
      "goto": 51
    },
    {
      "text": "你们俩，记住这个教训！信任是团队最重要的基石！",
      "type": "Center",
      "avatar": "陈",
      "goto": 52
    },
    {
      "text": "视频/2.mp4",
      "type": "Video",
      "goto": 53
    },
    {
      "text": "两人重重地点了点头，但张振的点头显得沉重而心虚。",
      "type": "Center",
      "goto": -1
    }
  ],
  "芯蚀-第七幕": [
    {
      "text": "实验室走廊，第二天。",
      "type": "Center",
      "goto": 1
    },
    {
      "text": "张振焦急地一次次拨打李维的电话，但始终无法接通。",
      "type": "Center",
      "goto": 2
    },
    {
      "text": "他又按照名片上的地址去找，发现那是一家空壳公司。",
      "type": "Center",
      "goto": 3
    },
    {
      "text": "他脸色惨白，背靠墙壁，冷汗直流。",
      "type": "Center",
      "goto": 4
    },
    {
      "text": "终于彻底明白自己上当受骗了。",
      "type": "Center",
      "goto": 5
    },
    {
      "text": "巨大的悔恨和恐惧淹没了他。",
      "type": "Center",
      "goto": 6
    },
    {
      "text": "他不知道自己泄露的东西会造成多大后果，更不敢向导师和林枫坦白。",
      "type": "Center",
      "goto": 7
    },
    {
      "text": ["不行...再拖下去，后果不可想象...教授一定会发现的...我不能再错下去！", "不能...一旦坦白，我就完了...我还有未来，我不能断送一切..."],
      "type": "Choice",
      "jump": [8, 32],
    },
    {
      "text": "走廊寂静，荧光灯嗡嗡作响。张振背靠墙壁，手心满是冷汗。",
      "type": "Center",
      "goto": 9
    },
    {
      "text": "他眼神涣散，手里攥着那张写有'加密邮箱'的纸条，纸角已经被揉得皱巴巴。",
      "type": "Center",
      "goto": 10
    },
    {
      "text": "不行...再拖下去，后果不可想象...教授一定会发现的...我不能再错下去！",
      "type": "Right",
      "avatar": "张",
      "goto": 11
    },
    {
      "text": "他猛地站起，几乎是跌跌撞撞地冲向陈教授办公室。",
      "type": "Center",
      "goto": 12
    },
    {
      "text": "台灯昏黄，陈教授正在批阅资料。林枫在一旁核对数据。",
      "type": "Center",
      "goto": 13
    },
    {
      "text": "教授，我...我有事要说。",
      "type": "Right",
      "avatar": "张",
      "goto": 14
    },
    {
      "text": "什么事？你看起来不对劲。",
      "type": "Center",
      "avatar": "陈",
      "goto": 15
    },
    {
      "text": "师兄，你怎么了？",
      "type": "Left",
      "avatar": "林",
      "goto": 16
    },
    {
      "text": "对不起...我...我把A部分的框架图交给了李维。",
      "type": "Right",
      "avatar": "张",
      "goto": 17
    },
    {
      "text": "你说什么？！你怎么能——",
      "type": "Left",
      "avatar": "林",
      "goto": 18
    },
    {
      "text": "张振，把经过全部说清楚。",
      "type": "Center",
      "avatar": "陈",
      "goto": 19
    },
    {
      "text": "李维骗我，说林枫可能泄露了B部分...我慌了...为了验证，我就...就把部分资料给了他。",
      "type": "Right",
      "avatar": "张",
      "goto": 20
    },
    {
      "text": "我以为是为了保护项目，可是...我错了！教授，林枫，对不起！",
      "type": "Right",
      "avatar": "张",
      "goto": 21
    },
    {
      "text": "沉默几秒，空气凝固。林枫脸色铁青，呼吸急促。陈教授深深叹了一口气。",
      "type": "Center",
      "goto": 22
    },
    {
      "text": "你知道这意味着什么吗？幸好你还愿意坦白。",
      "type": "Center",
      "avatar": "陈",
      "goto": 23
    },
    {
      "text": "教授，请您报警！不然损失会更大。我愿意承担所有后果！",
      "type": "Right",
      "avatar": "张",
      "goto": 24
    },
    {
      "text": "视频/5.mp4",
      "type": "Video",
      "goto": 25
    },
    {
      "text": "画面快切，黑衣人员在机场拘捕李维。",
      "type": "Center",
      "goto": 26
    },
    {
      "text": "新闻播报声响起，背景是'国家安全部门成功破获境外间谍案'的字幕。",
      "type": "Center",
      "goto": 27
    },
    {
      "text": "林枫和张振坐在窗边，老周端来两杯热咖啡。",
      "type": "Center",
        "goto": 28
    },
    {
      "text": "有些错，一旦犯下，可能会毁掉一切。你们算是守住了底线。",
      "type": "Center",
      "avatar": "老周",
      "goto": 29
    },
    {
      "text": "我差点害了整个项目...教授没有逐我出局，已经是最大的宽恕。",
      "type": "Right",
