<script setup>
/* eslint-disable */
import { watch, ref } from 'vue';
const props = defineProps(['title'])
const show = ref(false);
console.log(props.title)
watch(props, newval => {
    setTimeout(() => {
        show.value = true;
    }, 250)
    setTimeout(() => {
        show.value = false;
    }, 4000)
})
</script>

<template>
        <Transition name="title">
                <div v-if="show" id="box">
                    <p style="text-align: center;">{{props.title }}</p>
                </div>
        </Transition>
</template>

