<script setup>
/* eslint-disable */
import { ref, nextTick } from 'vue';
const props = defineProps(['text', 'type', 'jump', 'showif', 'index', 'color','avatar']);
const emit = defineEmits(['jumpto', 'pauseMusic', 'resumeMusic']);

const videoRef = ref(null);
const videoContainerStyle = ref({});
const isFullscreen = ref(false);
const isPlaying = ref(true);
const clickTimeout = ref(null);
const clickCount = ref(0);

const onVideoLoaded = () => {
  if (videoRef.value) {
    const video = videoRef.value;
    const aspectRatio = video.videoWidth / video.videoHeight;
    
    // 根据视频分辨率自适应边框大小
    const maxWidth = Math.min(800, window.innerWidth * 0.8);
    const maxHeight = Math.min(450, window.innerHeight * 0.6);
    
    let width, height;
    if (aspectRatio > maxWidth / maxHeight) {
      width = maxWidth;
      height = maxWidth / aspectRatio;
    } else {
      height = maxHeight;
      width = maxHeight * aspectRatio;
    }
    
    videoContainerStyle.value = {
      width: `${width}px`,
      height: `${height}px`,
      margin: '20px auto'
    };
    
    // 视频加载完成后自动播放，并暂停背景音乐
    emit('pauseMusic');
  }
};

const onVideoPlay = () => {
  // 视频开始播放时暂停背景音乐
  emit('pauseMusic');
};

const onVideoPause = () => {
  // 视频暂停时恢复背景音乐
  emit('resumeMusic');
};

const onVideoEnded = () => {
  // 视频结束时恢复背景音乐
  emit('resumeMusic');
};

const toggleFullscreen = () => {
  isFullscreen.value = !isFullscreen.value;
};

const togglePlayPause = () => {
  if (videoRef.value) {
    if (isPlaying.value) {
      videoRef.value.pause();
      isPlaying.value = false;
      emit('resumeMusic'); // 视频暂停时恢复背景音乐
    } else {
      videoRef.value.play();
      isPlaying.value = true;
      emit('pauseMusic'); // 视频播放时暂停背景音乐
    }
  }
};

const handleVideoClick = () => {
  clickCount.value++;
  
  if (clickCount.value === 1) {
    // 第一次点击，等待300ms看是否有第二次点击
    clickTimeout.value = setTimeout(() => {
      // 300ms内没有第二次点击，执行单击操作（暂停/播放）
      togglePlayPause();
      clickCount.value = 0;
    }, 300);
  } else if (clickCount.value === 2) {
    // 第二次点击，清除定时器，执行双击操作（全屏）
    clearTimeout(clickTimeout.value);
    toggleFullscreen();
    clickCount.value = 0;
  }
};

</script>

<template>
<div style="position: relative;">
    <el-text class="Left" v-if="props.type == 'Left'"
    :style="{ color: props.color || 'white', textShadow: props.color ? `0 0 5px ${props.color}, 0 0 20px ${props.color}` : '0 0 10px white, 0 0 20px white' }">
    
    <p><el-avatar :size=40 class="avatar-left" v-if="props.avatar!=null">{{ props.avatar }}</el-avatar>{{ props.text }}</p>
  </el-text>
  <el-text class="Right" v-if="props.type == 'Right'"
    :style="{ color: props.color || 'white', textShadow: props.color ? `0 0 5px ${props.color}, 0 0 20px ${props.color}` : '0 0 10px white, 0 0 20px white' }">
    <p>{{ props.text }}<el-avatar :size=40 class="avatar-right" v-if="props.avatar!=null">{{ props.avatar }}</el-avatar></p>
  </el-text>
  <el-text class="Center " v-if="props.type == 'Center'"><div class="centerdiv"><p>{{ props.text }}</p></div></el-text>
  <div class="Choice" v-if="props.type == 'Choice'">
    <el-text class="Choices" v-for="(i, ind) in props.text" :key="i">
      <p @click="$emit('jumpto', props.jump[ind], props.index)">{{ i }}</p>
    </el-text>
  </div>
  <div class="Left" v-if="props.type=='Image Left'">
    <el-avatar :size=40 class="avatar-left" v-if="props.avatar!=null">{{ props.avatar }}</el-avatar>
    <img class="image" :src="props.text" alt="Image Left"/>
  </div>
  <div class="Right" v-if="props.type=='Image Right'">
    <img class="image" :src="props.text" alt="Image Right"/>
    <el-avatar :size=40 class="avatar-right" v-if="props.avatar!=null">{{ props.avatar }}</el-avatar>
  </div>
  <div class="Video" v-if="props.type=='Video'">
    <!-- 自定义全屏容器 -->
    <div class="custom-fullscreen" :class="{ 'fullscreen-active': isFullscreen }">
      <div class="video-container" :style="videoContainerStyle">
        <video 
          :src="props.text" 
          autoplay 
          class="video-element"
          @loadedmetadata="onVideoLoaded"
          @play="onVideoPlay"
          @pause="onVideoPause"
          @ended="onVideoEnded"
          @click="handleVideoClick"
          ref="videoRef">
        </video>
        <div class="play-pause-overlay" @click="handleVideoClick">
          <div class="play-pause-icon" v-if="!isPlaying">
            ▶️
          </div>
        </div>
      </div>
    </div>
    <div class="video-info" v-if="!isFullscreen">
      <el-text>正在播放视频：{{ props.text }} (单击暂停/播放，双击全屏)</el-text>
    </div>
  </div>
</div>
</template>

<style scoped>
.avatar-left{
  text-shadow: none;
  position: relative;
  background-color: rgba(27, 27, 27,0);
  left: -2vh;
  top: -1vh;
}
.avatar-right{
  text-shadow: none;
  position: relative;
  background-color: rgba(27, 27, 27,0);
  left: 2vh;
  top: -1vh;
}
.image{
  position: relative;
  height: 20vh;
  width: 20*9/16vh;
  background-color: antiquewhite;
}
.div{
  position: relative;
}
.Choice {
  position: relative;
  bottom: 1vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-top: 15px;
}

.Choices>p {
  margin-top: -2px;
  transition: transform 0.5s ease, color 0.5s ease, text-shadow 0.5s ease;
}

.Choices>p:hover {
  transform: scale(1.1);
  color: white;
  text-shadow: 0 0 10px white, 0 0 20px white;
}

p::selection {
  color: aqua;
  text-shadow: 0 0 10px aqua, 0 0 20px aqua;
}

.Left {
  position: relative;
  left: 10px;
  text-align: left;
  line-height: 50px;
}

.Right {
  position: relative;
  right: 10px;
  text-align: right;
  line-height: 50px;
}

.Video {
  position: relative;
  width: 100%;
  margin: 20px 0;
  padding: 0;
  background-color: transparent;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.custom-fullscreen {
  position: relative;
  cursor: pointer;
  transition: all 0.3s ease;
}

.custom-fullscreen.fullscreen-active {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0.95);
  display: flex;
  justify-content: center;
  align-items: center;
}

.custom-fullscreen.fullscreen-active .video-container {
  width: 90vw !important;
  height: 90vh !important;
  margin: 0 !important;
  max-width: none;
  max-height: none;
}

.video-container {
  position: relative;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  transition: all 0.3s ease;
}

.video-container:hover {
  border-color: rgba(255, 255, 255, 0.4);
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.7);
  transform: scale(1.02);
}

.video-element {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  object-fit: contain;
  /* 完全隐藏所有视频控件 */
  -webkit-media-controls: none;
  -webkit-media-controls-panel: none;
  -webkit-media-controls-play-button: none;
  -webkit-media-controls-volume-slider: none;
  -webkit-media-controls-mute-button: none;
  -webkit-media-controls-timeline: none;
  -webkit-media-controls-current-time-display: none;
  -webkit-media-controls-time-remaining-display: none;
  -webkit-media-controls-fullscreen-button: none;
  -webkit-media-controls-start-playback-button: none;
  -webkit-media-controls-overlay-play-button: none;
  /* Firefox */
  -moz-media-controls: none;
  /* IE/Edge */
  -ms-media-controls: none;
}

.video-element:hover {
  filter: brightness(1.1);
}

.video-info {
  text-align: center;
  color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
  padding: 10px 0;
  margin-top: 10px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  width: 100%;
  max-width: 600px;
}

.play-pause-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.3);
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.video-container:hover .play-pause-overlay {
  opacity: 1;
}

.play-pause-icon {
  font-size: 48px;
  color: white;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.8);
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.play-pause-icon:hover {
  transform: scale(1.1);
  background-color: rgba(0, 0, 0, 0.7);
}
</style>
